        width: 76vw;
    }

   
    
}

@media screen and (max-width: 768px) {
    .Experince-div-2-img-div img {
        width: 76vw;
    }

    .Experince-div-2{
        padding: 20px 0px;
    }
    .Experince-div-wrapper{
        padding: 20px 0px;
        margin-top: 8vh;
    } 
    .Experience-div >p {
        /* border: 1px solid red; */
        font-size: 3vw;
    }

    .experince-strong{
        font-size: 3vw;
    }
    .Experience-div >h1 {
        /* border: 1px solid red; */
        font-size: 4vw;
    
    }
   
}

    @media screen and (max-width: 480px) {
